import React, { useEffect } from "react";
import logoimg from "../../Assets/imgs/logo.png";
import { Col, Container, Row } from "react-bootstrap";
import SectionTitle from "../../components/site/SectionTitle";
import MainButton from "../../components/shared/Buttons/MainButton";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { fetchProject } from "../../services/projects.service";
import FullScreenLoading from "../../components/shared/Loading/FullScreenLoading";
import { API_LINK_STORAGE } from "../../services/ApiService";

function ProjectDetails() {
  const dispatch = useDispatch();

  let { id } = useParams();

  const { projectsLoading, projectsError, project } = useSelector(
    (state) => state.projects
  );

  useEffect(() => {
    dispatch(fetchProject(id));
  }, [dispatch]);

  return (
    <FullScreenLoading loading={projectsLoading} error={projectsError}>
      {project.tools ? (
        <section id="projectDetailsSection">
          <Container>
            <SectionTitle title="عنوان المشروع" />
            <div className="project-header">
              <div className="project-info">
                <h3 className="project-part-title">{project.name}</h3>
                <p className="project-description">{project.description}</p>
              </div>
              <div className="project-logo-container">
                <div className="project-logo-ball">
                  <img src={API_LINK_STORAGE + project.logo} alt="" />
                </div>
                <div className="project-logo-shadow"></div>
              </div>
            </div>

            <div className="project-features">
              <h3 className="project-part-title">الميزات</h3>
              <Row className="justify-content-between align-items-center">
                {project.tools.map((item, idx) => {
                  return (
                    <Col key={idx} lg={2} md={4} xs={6}>
                      <div className="img-box">
                        <img src={API_LINK_STORAGE + item.img_path} />
                      </div>
                    </Col>
                  );
                })}
              </Row>
              <MainButton
                buttonText={
                  <a href={project.project_url} target="_blank">
                    <span>مشاهدة العمل</span>
                    <img src={logoimg} alt="" className="before-hover" />
                    <i className="fa fa-eye after-hover"></i>
                  </a>
                }
              />
            </div>
            <div className="project-view">
              <h3 className="project-part-title">المعرض</h3>
              <Row className="justify-content-center align-items-center">
                <Col lg={1} md={3}></Col>
                <Col lg={4} md={9}>
                  {project.images[0] ? (
                    <div className="img-box">
                      <img
                        src={API_LINK_STORAGE + project.images[0].img_path}
                        alt=""
                      />
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
                <Col lg={2}></Col>
                <Col lg={4} md={9}>
                  {project.images[1] ? (
                    <div className="img-box">
                      <img
                        src={API_LINK_STORAGE + project.images[1].img_path}
                        alt=""
                      />
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
                <Col lg={1} md={3}></Col>
                <Col lg={4} md={3}></Col>
                <Col lg={4} md={9}>
                  {project.images[2] ? (
                    <div className="img-box">
                      <img
                        src={API_LINK_STORAGE + project.images[2].img_path}
                        alt=""
                      />
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
                <Col lg={4}></Col>
                <Col lg={1}></Col>
                <Col lg={4} md={9}>
                  {project.images[3] ? (
                    <div className="img-box">
                      <img
                        src={API_LINK_STORAGE + project.images[3].img_path}
                        alt=""
                      />
                    </div>
                  ) : (
                    ""
                  )}
                </Col>
                <Col lg={2} md={3}></Col>
                <Col lg={4} md={12}>
                  <Row>
                    <Col md={3}></Col>
                    <Col lg={12} md={9}>
                      {project.images[4] ? (
                        <div className="img-box">
                          <img
                            src={API_LINK_STORAGE + project.images[4].img_path}
                            alt=""
                          />
                        </div>
                      ) : (
                        ""
                      )}
                    </Col>
                  </Row>
                </Col>
                <Col lg={1}></Col>
              </Row>
            </div>
          </Container>
        </section>
      ) : (
        <FullScreenLoading />
      )}
    </FullScreenLoading>
  );
}

export default ProjectDetails;
